import packages from '../../packages'
let Message = packages.Message
const vCopy = {
  /*
    bind é©å­å½æ°ï¼ç¬¬ä¸æ¬¡ç»å®æ¶è°ç¨ï¼å¯ä»¥å¨è¿éååå§åè®¾ç½®
    el: ä½ç¨ç dom å¯¹è±¡
    value: ä¼ ç»æä»¤çå¼ï¼ä¹å°±æ¯æä»¬è¦ copy çå¼
  */
  bind (el, { value, expression }) {
    el.$value = value || expression // åéåå­ç¬¦ä¸²é½æ¯æ
    el.handler = () => {
      if (!el.$value) {
        Message.warning('æ å¤å¶åå®¹')
        return
      }
      // å¨æåå»º textarea æ ç­¾
      const textarea = document.createElement('textarea')
      textarea.readOnly = 'readonly'
      textarea.style.position = 'absolute'
      textarea.style.left = '-9999px'
      // å°è¦ copy çå¼èµç» textarea æ ç­¾ç value å±æ§
      textarea.value = el.$value
      // å° textarea æå¥å° body ä¸­
      document.body.appendChild(textarea)
      // éä¸­å¼å¹¶å¤å¶
      textarea.select()
      // textarea.setSelectionRange(0, textarea.value.length);
      const result = document.execCommand('Copy')
      if (result) {
        Message('å¤å¶æå')
      }
      document.body.removeChild(textarea)
    }
    // ç»å®ç¹å»äºä»¶ï¼å°±æ¯æè°çä¸é® copy å¦
    el.addEventListener('click', el.handler)
  },
  // å½ä¼ è¿æ¥çå¼æ´æ°çæ¶åè§¦å
  componentUpdated (el, { value, expression }) {
    el.$value = value || expression
  },
  // æä»¤ä¸åç´ è§£ç»çæ¶åï¼ç§»é¤äºä»¶ç»å®
  unbind (el) {
    el.removeEventListener('click', el.handler)
  }
}

export default vCopy
